:root {
  --color-bg: #e8eaed;
  --color-fg: #000000;
  --color-accent: #ffffff;
  --color-border: #b6b6b6;
  --color-blue: #4a7af5;
  --color-red: #f54a4a;
  font-size: 62.5%;
}

* {
  -webkit-tap-highlight-color: transparent;
}

html {
  background-color: var(--color-bg);
  color: var(--color-fg);
  overflow-x: hidden;
}

a {
  color: inherit;
  text-decoration: none;
}

p {
  user-select: none;
}

html,
body {
  -webkit-overflow-scrolling: touch;
  overscroll-behavior: contain;
}

header {
  width: 100%;
  height: 5rem;
  background: var(--color-accent);
  border-bottom: 1px solid var(--color-border);
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  position: sticky;
  top: 0;
  margin-bottom: 2.8rem;
  padding-block: 0.2rem;
  z-index: 1;
}

header > p {
  font-size: 2.5rem;
  font-weight: bold;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media (min-width: 300px) and (max-width: 350px) {
  header > p {
    max-width: 220px;
  }
}

@media (min-width: 350px) and (max-width: 400px) {
  header > p {
    max-width: 270px;
  }
}

@media (min-width: 400px) and (max-width: 450px) {
  header > p {
    max-width: 320px;
  }
}

.collapsible-btn {
  border: none;
  background: var(--color-accent);
  margin-top: 0.5rem;
  margin-right: 0.5rem;
  margin-left: 0.5rem;
  cursor: pointer;
}

.sidebar {
  background-color: var(--color-bg);
  height: 100vh;
  width: 0;
  z-index: 5;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  position: fixed;
  top: 0;
  left: 0;
  transition: 0.2s;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sidebar-title {
  font-size: 3rem;
  font-weight: bold;
  opacity: 0;
  transition: all 0.2s;
}

.sidebar-item {
  width: 90%;
  min-height: 2rem;
  border-radius: 10px;
  margin-bottom: 1.2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  transition:
    opacity 0.1s,
    background-color 0.3s;
}

.sidebar-item--normal {
  background-color: var(--color-accent);
  cursor: pointer;
}

.sidebar-item--active {
  background-color: var(--color-blue);
}

.sidebar-item-icon {
  width: 27px;
  height: 24px;
  margin-left: 1.5rem;
}

.sidebar-item--normal > .sidebar-item-icon {
  fill: var(--color-fg);
  stroke: var(--color-fg);
}

.sidebar-item--active > .sidebar-item-icon {
  fill: var(--color-accent);
  stroke: var(--color-accent);
}

.sidebar-item > p {
  font-size: 1.8rem;
  margin-left: 1rem;
}

.sidebar-item--normal > p {
  color: var(--color-fg);
}

.sidebar-item--active > p {
  color: var(--color-accent);
}

.items-left {
  color: white;
  font-size: 2rem;
  position: absolute;
  right: 1rem;
  margin-right: 0.5rem;
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.background {
  background-color: #000000;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 1;
  transition: 0.1s;
  position: fixed;
  top: 0;
  left: 0;
}

.add-button-wrapper {
  -webkit-appearance: none;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  position: fixed;
  bottom: 0;
  margin-bottom: 2rem;
}

.add-button {
  -webkit-appearance: none;
  background-color: var(--color-accent);
  width: 5.6rem;
  height: 5.6rem;
  border: solid 1px var(--color-border);
  border-radius: 60px;
  font-size: 4rem;
  color: var(--color-border);
  cursor: pointer;
  user-select: none;
  transition: opacity 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.add-button:active {
  -webkit-appearance: none;
  opacity: 50%;
}

.modal-wrapper {
  -webkit-appearance: none;
  height: calc(100vh - 20vh);
  width: 100vw;
  overflow-y: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5;
  transition: 0.1s;
  position: absolute;
}

.modal {
  background-color: var(--color-accent);
  border-radius: 20px;
  width: 31.5rem;
  height: 32.2rem;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  z-index: 2;
}

.modal-title {
  text-align: center;
  font-size: 4rem;
  font-weight: bold;
  margin-top: 0;
  margin-bottom: 5.2rem;
}

.modal-settings-name {
  display: flex;
  flex-direction: row;
  margin-bottom: 1.6rem;
}

.modal-settings-name > p {
  font-size: 2.5rem;
  margin: 0;
}

.modal-settings-name > input {
  font-size: 2.5rem;
  background-color: var(--color-accent);
  border: none;
  width: 16.5rem;
  padding: 0;
  margin-left: 1rem;
  white-space: pre-wrap;
}

.modal-settings-name > input:focus {
  outline: none;
  border-bottom: 2px solid var(--color-fg);
}

.modal-settings-public {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.modal-settings-public > p {
  font-size: 2.5rem;
  margin: 0;
}

.modal-settings-autoformat {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-right: 1.1rem;
  margin-left: 6rem;
  white-space: nowrap;
}

.modal-settings-autoformat > p {
  font-size: 2.5rem;
  margin: 0;
}

.modal-switch {
  margin-left: 1.3rem;
  margin-right: 13.6rem;
}

.switch {
  position: relative;
  display: inline-block;
  width: 3.4rem;
  height: 2rem;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-border);
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  width: 1.6rem;
  height: 1.6rem;
  left: 0.2rem;
  bottom: 0.25rem;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: var(--color-blue);
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--color-blue);
}

input:checked + .slider:before {
  -webkit-transform: translateX(13px);
  -ms-transform: translateX(13px);
  transform: translateX(13px);
}

.slider.round {
  border-radius: 40px;
}

.slider.round:before {
  border-radius: 50%;
}

.modal-buttons {
  display: flex;
  width: 80%;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 4.5rem;
}

.modal-buttons > button {
  background: var(--color-accent);
  border: solid 2px;
  border-radius: 10px;
  width: 8.6rem;
  height: 3.9rem;
  cursor: pointer;
}

.modal-buttons > button:last-child {
  border-color: var(--color-blue);
  color: var(--color-blue);
  font-weight: bold;
  transition: all 0.1s;
}

.modal-buttons > button:last-child:active {
  background-color: var(--color-blue);
  color: var(--color-accent);
}

.modal-buttons > button:first-child {
  border-color: var(--color-red);
  color: var(--color-red);
  font-weight: bold;
  transition: all 0.1s;
}

.modal-buttons > button:first-child:active {
  background-color: var(--color-red);
  color: var(--color-accent);
}

.list-container {
  position: absolute;
  top: 7rem;
  bottom: 7.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
  overflow-x: hidden;
}

.list-item-wrapper {
  width: 100vw;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 0.8rem;
}

.list-item {
  background-color: var(--color-accent);
  width: 90%;
  font-size: 1.8rem;
  border-radius: 10px;
  min-height: 4.5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  opacity: 100%;
  transition: opacity 0.2s;
  user-select: none;
}

.list-item.removed {
  transform: translateX(-100%);
}

.list-item:active {
  opacity: 60%;
}

.list-item-label {
  width: auto;
  margin-right: 2rem;
  word-wrap: break-word;
  overflow: auto;
  transition: color 0.1s;
  margin-left: 2rem;
}

.list-item-public {
  background-color: var(--color-blue);
  width: 2.4rem;
  height: 2.4rem;
  margin-left: 2rem;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-accent);
  font-weight: bold;
}

.list-item-format {
  background-color: var(--color-red);
  width: 2.4rem;
  height: 2.4rem;
  margin-left: 2rem;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-accent);
  font-weight: bold;
}

.list-item-public + .list-item-format {
  margin-left: 0.5rem;
}

.list-item-public + .list-item-label,
.list-item-format + .list-item-label {
  margin-left: 1rem;
}

.list-item > input {
  width: 100%;
  margin-inline: 2rem;
  padding-block: 1.8rem;
  font-size: 1.8rem;
  border: none;
  background-color: var(--color-accent);
  font-family: inherit;
}

.list-item > input:focus {
  outline: none;
}

.swipeable {
  transition: transform 0.2s;
}

.about {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.about-title {
  font-weight: bold;
  font-size: 4rem;
  margin-top: 2.5rem;
  margin-bottom: 0;
}

.about-version {
  color: var(--color-border);
  font-size: 2rem;
  font-weight: bold;
  margin-left: 50%;
}

.about-id {
  color: var(--color-border);
  font-size: 1.8rem;
  font-weight: bold;
  position: absolute;
  bottom: 0;
  left: 2rem;
}

.icon {
  width: 172px;
  height: 172px;
}

.add-item-wrapper {
  position: fixed;
  bottom: 0;
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 1.5rem;
  margin-inline: 1rem;
}

.add-item-wrapper > input {
  background-color: var(--color-accent);
  border: 1px solid var(--color-border);
  border-radius: 30px;
  width: 70%;
  font-size: 2rem;
  padding-inline: 2rem;
}

.add-item-wrapper > input:focus {
  outline: none;
}

.add-item-wrapper > button {
  margin-right: 1.7rem;
}

.cross-line {
  height: 0.3rem;
  position: absolute;
  background-color: var(--color-border);
  margin-left: 2rem;
}
